*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#app-container{
    background-color: rgb(50,158,197);
    display: flex;
    min-height: 100vh;
    width: 100%;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
}
.header{
    position:fixed;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    padding: 5% 6% 0% 4%;
}

.content{
    padding: 0% 0;
    width: 100%;
}
#footer{
    margin-top: auto;
    text-align: center;
    padding: 3%;
    color:white;
}
.nav-bar,.menu .active{
